/* MapPage */
.map-page-container{
  width: 100vw;
  height: 100vh;
}
.nav-container{
  position: fixed;
  top: 0;
  left: 0;
  padding: 10px;

  width: 100vw;
  height: 60px;
  
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
  /* background: #aaa; */
}

.nav-left-container{
  /* background: #000; */
  height: 100%;
  display: flex;
  position: relative;
}
.nav-button{
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 5px;
  height: 40px;

  width: 350px;
  position: absolute;
  z-index: 30;
  overflow: hidden;
  display: flex;
  background: #fff;
}


.nav-logo-container{
  height: 40px;
  width: 120px;
  background: var(--color3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  font-size: 1.1rem;
  cursor: pointer;
}
.nav-logo-container p{
  font-size: 1rem;
  font-weight: 700;
  color: #efefef;
  user-select: none;
}
.nav-logo-container .menu-icon{
  color: #efefef;
}
.nav-logo-container .logo-icon{
  color: var(--color7);
}


.nav-input-container{
  height: 40px;
  width: 230px;
  padding: 0 10px;
  display: flex;
  align-items: center;
}
.nav-input-container form{
  display: flex;
  align-items: center;
}
.nav-input-container input{
  all: unset;
  height: 40px;
  width: 185px;
  font-weight: bold;
  font-size: .9rem;
}
.nav-input-container button{
  all: unset;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-icon{
  font-size: 1.2rem;
  font-weight: bold;
  margin-left: 5px;
  color: #aaa;
  cursor: pointer;
}
.search-icon:hover{
  color: var(--color1);
}
.nav-button-text{
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 50vh;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 5px 10px;
  background: #fff;
}
.nav-button-text p{
  margin: 0;
  margin-left: 5px;
  padding: 0;
  font-size: .8rem;
  font-weight: bold;
  user-select:none
}
.nav-button-text:active{
  box-shadow: #c1c1c1 0px 1px 4px;
}

.nav-button-text.on.flood{
  box-shadow: var(--flood) 5px 5px 7px;
  height: 36px;
  border: 3px solid var(--flood);
  margin-left: 4.5px;
}
.nav-button-text.on.earthquake{
  box-shadow: var(--earthquake) 5px 5px 7px;
  height: 36px;
  border: 3px solid var(--earthquake);
  margin-left: 4.5px;
}
.nav-button-text.on.temporary{
  box-shadow: var(--temporary) 5px 5px 7px;
  height: 36px;
  border: 3px solid var(--temporary);
  margin-left: 4.5px;
}
.nav-button-text.on.hot{
  box-shadow: var(--swelter) 5px 5px 7px;
  height: 36px;
  border: 3px solid var(--swelter);
  margin-left: 4.5px;
}
.nav-button-text.on.cold{
  box-shadow: var(--severeCold) 5px 5px 7px;
  height: 36px;
  border: 3px solid var(--severeCold);
  margin-left: 4.5px;
}


.nav-flood-poly-button-container{
  position: relative;
  width: 100px;
  height: 0px;
}
.nav-flood-poly-button{
  position: absolute;
  top: -15px;
  left: 0;
  width: 85px;
  height: 40px;
  background: var(--flood);
  color: #efefef;
  box-shadow: #0c1a51 0px 2px 10px, #5e72c0 0px -0.5px 5px;
  border-radius: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.nav-flood-poly-button.off{
  background: #c1c8e4;
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  color: #333;
}
.nav-flood-poly-button.off svg{
  color: #243579;
}

.nav-flood-poly-button svg{
  color: #243579;
}
.nav-flood-poly-button p{
  margin: 0;padding: 0;
  font-size: .85rem;
  margin-left: 3px;
  font-weight: bold;
  user-select: none;
}
.nav-delete-marker-button-container{
  position: relative;
  width: 100px;
  height: 0px;
}
.nav-delete-marker-button{
  position: absolute;
  top: -15px;
  left: 0;
  width: 85px;
  height: 40px;
  background: rgb(184, 58, 58);
  color: #efefef;
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.nav-delete-marker-button svg{
  color: #5f0707;
}
.nav-delete-marker-button p{
  margin: 0;padding: 0;
  font-size: .85rem;
  margin-left: 3px;
  font-weight: bold;
  user-select: none;
}
.nav-category-button-container{
  position: relative;
  /* background: #000; */
  width: 40px;
  height: 40px;
  margin-right: 30px;
}
.nav-category-toggle-button{
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 50vh;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  font-size: .85rem;
  font-weight: bold;
  user-select: none;
}
.nav-category-button{
  /* box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 50vh;
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: #fff; */
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 130px;
  top: 0;
  left: 0;
  transform: translateX(-30%);
  opacity: 0;
  transition: .3s;
  user-select: none;
  cursor: pointer;
  pointer-events: none;
}
.nav-category-button.first-drop{
  transition: .3s;
  top: 55px;
  opacity: 1;
  pointer-events: all;
}
.nav-category-button.second-drop{
  transition: .3s;
  top: 95px;
  opacity: 1;
  pointer-events: all;
}
.nav-category-button.third-drop{
  transition: .3s;
  top: 135px;
  opacity: 1;
  pointer-events: all;
}



.nav-button-profile{
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 50vh;
  height: 40px;
  width: 40px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: #fff;
}
.nav-button-profile:active{
  box-shadow: #c1c1c1 0px 1px 4px;
}
.nav-button-profile{
  font-size: 1.5rem;
}





.flood-icon{
  color: var(--flood);
}
.earthquake-icon{
  color: var(--earthquake);
}
.swelter-icon{
  color: var(--swelter);
}
.severeCold-icon{
  color: var(--severeCold);
}
.temporary-icon{
  color: var(--temporary);
  /* font-size: .85rem; */
}

.nav-right-container{
  display: flex;
  align-items: center;
  z-index: 10;
}
.nav-right-container>.nav-button-text{
  margin-left: 10px;
}

.right-map-button-container{
  z-index: 10;
  display: flex;
  flex-direction: column;
  position: fixed;
  right: 0;
  top: 50%;
  transform: translate(0,-120px);
  width: 50px;
  height: 100px;
  /* background: #000; */
  padding: 10px;
}
.right-map-button{
  width: 30px;
  height: 30px;
  border: 1px solid #333;

  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  border-radius: 5px;
  background: #fff;
  cursor: pointer;
  user-select: none;

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  margin-bottom: 10px;
}
.right-map-button:active{
  box-shadow: #c1c1c1 0px 1px 4px;
}
.right-map-button p{
  font-size: .7rem;
  margin: 0;
  padding: 0;
  font-weight: bold;
  color: #ff4120
}

.reset-button-container{
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  width: fit-content;
  height: 70px;
  z-index: 10;
}
.reset-button{
  border-radius: 50vh;
  padding: 0 10px;
  width: 100%;
  height: 100%;
  background-color: #110034;
  box-shadow: #969696 0px 2px 3px, #a5a5a5 0px -0.5px 2px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.reset-button.vibration {
  animation: vibration .1s infinite;
}
@keyframes vibration {
  from {
    transform: rotate(1.5deg);
  }
  to {
    transform: rotate(-1.5deg);
  }
}

.reset-button svg{
  font-size: 1.2rem;
  margin-right: 5px;
}
.reset-button .find-marker-icon{
  color: var(--color7);
}
.reset-button p{
  margin: 0;
  padding: 0;
  font-weight: bold;
  user-select: none;
}
.reset-button p:nth-child(1){
  margin-right: 5px;
}
.reset-button:active{
  box-shadow: #c1c1c1 0px 1px 4px;

}

.map{
  user-select: none;
  z-index: 0;
  width: 100vw;
  height: 100vh;
}

